:root {
  --color-green: #4aaf47;
  --color-dark: #141414;
  --color-light-grey: #e4e4e4;
  --color-dark-grey: #a2a2a2;
  --color-text-dgrey: #555555;
  --color-text-lgrey: #858585;
  --font-ui-regular: "Circular Regular";
  --font-ui-medium: "Circular Medium";
  --font-ui-bold: "Circular Bold";
}

* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--font-ui-regular);
  background: var(--color-light-grey);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a,
button {
  text-decoration: none;
  display: inline-block;
  color: inherit;
  transition: 0.3s all ease;
}

a:hover,
a:focus,
button:hover,
button:focus {
  transition: 0.3s all ease;
  box-shadow: none !important;
  outline: 0 !important;
  text-decoration: none;
}

.anim-hover {
  position: relative;
}

.anim-hover::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  transition: 0.3s all ease;
  background: var(--color-black);
  width: 0;
  border-radius: 6px;
}

.anim-hover:hover::before {
  width: 100%;
  left: 0;
  transition: 0.3s all ease;
}

.anim-hover span {
  z-index: 2;
  position: relative;
  color: var(--color-white);
}

img {
  width: 100%;
  height: auto;
  vertical-align: middle;
}

/* fonts */

@font-face {
  font-family: "Circular Regular";
  src: url(fonts/Circular-regular.ttf);
}

@font-face {
  font-family: "Circular Medium";
  src: url(fonts/Circular-Medium.ttf);
}

@font-face {
  font-family: "Circular Bold";
  src: url(fonts/Circular-Bold.ttf);
}

.font-ui-med {
  font-family: var(--font-ui-medium);
}

.font-ui-bold {
  font-family: var(--font-ui-bold);
}
select {
  position: relative;
  background-image: url(../assets/images/chevrondown.svg) !important;
  background-repeat: no-repeat;
  background-size: 11px 8px;
  background-position: 90% center !important;
  padding-right: 22px !important;
}
