/* header */

header {
  background: #fff;
}

header .desktop-header {
  display: none;
}

@media (min-width: 768px) {
  header .desktop-header {
    display: block;
  }
  header .mob-header {
    display: none;
  }
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 17px;
}

header .nav-news {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 58%;
}

.nav-news div.gift {
  width: 32%;
  font-size: 16px;
  color: var(--color-dark);
}

.nav-news .gift img {
  max-width: 36px;
  margin-right: 13px;
}

.nav-news .winner {
  width: 46%;
  color: var(--color-text-dgrey);
  font-size: 16px;
}

.nav-news .winner span {
  color: var(--color-dark);
}

.nav-news .learn {
  width: 15%;
}

.nav-news .learn a {
  display: flex;
  align-items: center;
  color: var(--color-green);
}

.nav-news .learn a img {
  margin-left: 10px;
  max-width: 16px;
}

header nav ul.main {
  display: flex;
  align-items: center;
  padding: 27px 14px 25px 0;
}

header nav ul.main li:last-of-type a {
  padding: 0 0 0 10px;
}

header nav ul.main li a {
  padding: 0 18px;
  font-size: 15px;
  color: #494949;
}

header nav ul.main li a img {
  max-width: 24px;
  vertical-align: -1px;
}

header nav ul.main li.notification a {
  padding: 0 21px 0 25px;
}

header nav ul.main li.notification a img {
  max-width: 22px;
}

header nav ul.main li.dropdown ul {
  right: 0;
  left: unset !important;
}

/* ====== Aside ======== */
section#main {
  position: relative;
}
aside {
  position: absolute;
  width: 88px;
  height: 100%;
  min-height: 100vh !important;
  background: #fff;
  transition: 0.3s all ease;
  z-index: 10;
  overflow: hidden;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (min-width: 2200px) {
  aside {
    height: 100vh;
  }
}

@media (min-width: 1700px) {
  aside {
    width: 3.5%;
    height: -webkit-fill-available;
  }
}

aside .sidebar-links {
  padding: 37px 29px 0;
  transition: 0.3s all ease;
}

aside .sidebar-footer {
  border-top: 1px solid #eaeaea;
  padding: 25px 22px 44px;
}

@media (min-width: 768px) {
  aside:hover .sidebar-links {
    padding: 37px 22px 0 21px;
    transition: 0.3s all ease;
  }
}

.sidebar-links .btn {
  font-family: var(--font-ui-medium);
  margin-bottom: 44px;
  position: relative;
  padding: 0;
  width: 252px !important;
  color: #fff;
  display: block;
  text-align: left;
  color: var(--color-text-dgrey);
}

@media (min-width: 2100px) {
  .sidebar-links .btn {
    font-size: 19px;
    width: 300px !important;
  }
  aside:hover .sidebar-links .btn img,
  aside:hover .sidebar-footer .btn img {
    max-width: 40px !important;
    margin-right: 10px;
  }
}

.sidebar-links .btn img,
.sidebar-footer .btn img {
  max-width: 26px;
  margin-right: 9px;
  transition: 0.3s all ease;
}

.sidebar-footer .btn {
  display: flex;
  align-items: center;
  width: 252px !important;
}

.sidebar-footer .btn img {
  max-width: 40px;
  transition: 0.3s all ease;
}

.sidebar-footer .btn span p:first-of-type {
  font-size: 16px;
  color: var(--color-text-dgrey);
  text-align: left;
}

.sidebar-footer .btn span p:last-of-type {
  color: #3a65e3;
  font-size: 12px;
  text-align: left;
}

.sidebar-footer .btn span.arrow img {
  margin-left: 20px;
  max-width: 10px;
}

@media (min-width: 768px) {
  .sidebar-links a span,
  .sidebar-footer a span {
    display: none;
    transition: 0.3s all ease;
    transition-delay: 0.3s;
  }
}

@media (min-width: 2100px) {
  .sidebar-links .btn img,
  .sidebar-footer .btn img {
    max-width: 45px;
  }
}

@media (min-width: 2700px) {
  .sidebar-links .btn img,
  .sidebar-footer .btn img {
    max-width: 47px;
  }
  aside:hover .sidebar-links .btn img,
  aside:hover .sidebar-footer .btn img {
    max-width: 50px !important;
  }
  .sidebar-links .btn,
  .sidebar-footer .btn {
    font-size: 22px;
    width: 400px !important;
  }
}

aside:hover {
  width: 18.6%;
  transition: 0.3s all ease;
}

@media (min-width: 1700px) {
  aside:hover {
    width: 17.6%;
  }
  .row1 {
    display: flex;
    height: 100vh;
  }
}

@media (max-width: 1380px) {
  aside:hover {
    width: 21.6%;
  }
}

@media (min-width: 768px) {
  aside:hover .sidebar-links a span,
  aside:hover .sidebar-footer a span {
    display: inline-block;
    transition: 0.3s all ease;
  }
  aside:hover .sidebar-links .btn img {
    max-width: 24px;
    transition: 0.3s all ease;
  }
  aside:hover .sidebar-links a span,
  aside:hover .sidebar-footer a span {
    transition-delay: 0.3s;
  }
}

/* Main Section */

