}

.news-col .news-content label {
  color: var(--color-green);
  font-size: 14px;
  margin-bottom: 5px;
}

.news-col .news-content h3 {
  font-size: 18px;
  font-weight: 400;
  color: #232a45;
}

.news-col .news-content p {
  color: var(--color-text-lgrey);
  font-size: 14px;
  overflow: hidden;
  word-wrap: break-word;
  display: -webkit-box;
  white-space: normal;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  margin: 9px 0 11px;
}

.news-content .news-time-container span {
